.header {
	&__social {
		margin: 30px 0;
		padding: 17px 0; }
	&__logo {
		padding: 5px 0;
		max-width: 360px;
		&-circle {
			top: 70px;
			height: 100px;
			&:before {
				height: 360px; } } } }
.logo {
	display: inline-block; }
.logo_sm {
	display: none; }
.btn-nav {
	display: none; }
.menu {
	height: 50px;
	&__link {
		display: none !important;
		margin-right: 140px; } }
.nav {
	&__list {
		li,
		a {
			display: inline-block; }
		a {}
		letter-spacing: 0.08em; } }
.social {
	&__mail {
		@include f(null, 24, 30);
		font-weight: bold;
		letter-spacing: -0.03em; } }
.phones {
	&__item,
	&__link {
		display: inline-block; }
	&__item {
		&:not(:last-child) {
			&:after {
				display: inline-block; } } }
	&__link {
		@include f('ll', 18, 30); } }
.tel--hiden p {
	font-size: 20px; }
.hidden__content {
	font-size: 2.1rem;
	letter-spacing: 0.03em; }
.tel__text--hidden {
	font-size: 2.1rem;
	letter-spacing: 0.02em; }
.tel__eye {
	top: 7px;
	right: 0;
	width: 60px;
	height: 30px;
	background-image: url(../img/block__phone__eye.svg), linear-gradient(to right,rgba(36, 51, 67,0),rgba(36, 51, 67,0.96) 32%, rgb(36, 51, 67)); }
